import React from 'react';

import Layout from '../components/layout';
import SEO from '../components/seo';
import Hero from '../components/homepage/hero';
import Stats from '../components/homepage/stats';
import Section from '../components/homepage/section';
import UseCases from '../components/homepage/use_cases';
import Partners from '../components/homepage/partners';

import content from '../../site/content/homepage.json';
import themes from '../../site/style/themes.json';
import { graphql } from 'gatsby';

interface Props {
  pageContext: {
    locale: string;
    canonical: boolean;
    alternatePages: {
      slug: string;
      locale: string;
    }[];
  };
  data: {
    useCases: {
      edges: {
        node: {
          frontmatter: {
            thumbnail: string;
            title: string;
          };
          fields: {
            localizedSlug: string;
          };
        };
      }[];
    };
  };
}

const Home = ({ pageContext, data }: Props) => {
  const { locale, canonical, alternatePages } = pageContext;
  const useCases = data.useCases.edges.map(({ node }) => ({
    thumbnail: node.frontmatter.thumbnail,
    title: node.frontmatter.title,
    slug: node.fields.localizedSlug,
  }));
  const sections = content[locale].sections;
  return (
    <>
      <SEO
        title={content[locale].seo.title}
        description={content[locale].seo.description}
        image={content[locale].seo.image}
        locale={locale}
        canonical={canonical}
        alternatePages={alternatePages}
      />
      <Layout locale={locale}>
        <div id="homepage-content">
          <Hero
            locale={locale}
            title={content[locale].title}
            intro={content[locale].intro}
            news={content[locale].news}
          />
          {/* {content[locale].stats && <Stats stats={content[locale].stats} />} */}
          {sections &&
            sections.map((section, i) => {
              const theme = themes.themes.find(theme => theme.name == section.theme);
              const nextTheme = sections[i + 1]
                ? themes.themes.find(theme => theme.name == sections[i + 1].theme)
                : { background_color: 'blue' };
              return <Section section={section} theme={theme} nextTheme={nextTheme} />;
            })}
          {/* USE CASES PART */}
          {content[locale].use_cases && (
            <UseCases useCases={useCases} section={content[locale].use_cases} />
          )}
          {/* PARTNERS PART */}
          {content[locale].partners && <Partners partners={content[locale].partners} />}
        </div>
      </Layout>
    </>
  );
};

export default Home;

export const query = graphql`
  query ($locale: String!) {
    useCases: allMarkdownRemark(
      filter: { fields: { locale: { eq: $locale }, localizedSlug: { regex: "/use-cases/" } } }
      limit: 3
    ) {
      edges {
        node {
          frontmatter {
            thumbnail
            title
          }
          fields {
            localizedSlug
          }
        }
      }
    }
  }
`;
