import React from 'react';

interface Props {
  partners: {
    title: string;
    keyword: string;
    description: string;
    all: {
      logo: string;
      title: string;
      link: string;
    }[];
  };
}

const Partners = ({ partners }: Props) => (
  <div className="py-16 md:py-32 bg-gradient-to-b from-blue-light to-black">
    <div className="container">
      <h1 className="text-5xl sm:text-6xl md:text-9xl text-grey">
        {partners.title} <b className="text-white">{partners.keyword}</b>
      </h1>
      <p className="text-white">{partners.description}</p>
      <div className="py-12 md:flex md:justify-center">
        {partners.all.map(partner => (
          <a
            href={partner.link}
            className="h-24 mx-8 relative md:w-64 block"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img src={partner.logo} />
          </a>
        ))}
      </div>
    </div>
  </div>
);

export default Partners;
