import React from 'react';

interface Props {
  section: {
    title: string;
    keyword: string;
    description: string;
    align: 'right' | 'left' | 'alternate';
    cards: {
      icon: string;
      title: string;
      description: string;
    }[];
  };
  theme: {
    background_color: string;
    title_color: string;
    keyword_color: string;
    description_color: string;
    icon_color: string;
  };
  nextTheme: {
    background_color: string;
  };
}

const Section = ({ section, theme, nextTheme }: Props) => (
  <div
    className={`py-16 md:py-32 bg-gradient-to-b from-${theme.background_color} to-${nextTheme.background_color} pb-10`}
  >
    <div className="container">
      <h1 className={`text-5xl sm:text-6xl md:text-9xl text-${theme.title_color}`}>
        {section.title} <b className={`text-${theme.keyword_color}`}>{section.keyword}</b>
      </h1>
      <p className={`text-${theme.description_color}`}>{section.description}</p>
      <div className="py-12">
        {section.cards.map((card, i) => {
          const reverse = section.align == 'left' || (section.align == 'alternate' && i % 2 == 0);
          return (
            <div
              className={`py-8 md:flex md:py-24 md:items-center ${
                reverse && 'md:flex-row-reverse'
              }`}
            >
              <div className="my-4 md:flex-1">
                <img src={card.icon} className={`icon-${theme.icon_color} mx-auto h-24`} />
              </div>
              <div className="text-center md:flex-1 md:text-left">
                <h2 className={`text-3xl text-${theme.title_color} font-bold`}>{card.title}</h2>
                <p className={`text-${theme.description_color} max-w-lg`}>{card.description}</p>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  </div>
);

export default Section;
