import React, { useState } from 'react';
import getNewsletterURI from '../../utils/getNewsletterUri';
import Notification from '../notification';

import suscribeIcon from '../../images/icons/subscribe.svg';

interface Props {
  locale: string;
  title: string;
  intro: string;
  news: {
    title: string;
    description: string;
    link: string;
    image: string;
  };
}

const labels = {
  newsletter: {
    en: 'Join newsletter',
    fr: 'Rejoindre la newsletter',
  },
};

const Hero = ({ locale, title, intro, news }: Props) => {
  const [email, setEmail] = useState('');
  const [honeypotEmail, setHoneypotEmail] = useState(undefined);
  const [success, setSuccess] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(undefined);

  const handleClose = () => {
    setSuccess(false);
    setError(false);
  };

  const handleSubmit = async evt => {
    try {
      if (honeypotEmail) return;
      const newsletterURI = getNewsletterURI();
      setLoading(true);
      evt.preventDefault();
      setError(undefined);
      const { status } = await fetch(`${newsletterURI}/${locale}/contact`, {
        method: 'POST',
        headers: { accept: 'application/json', 'content-type': 'application/json' },
        body: JSON.stringify({ email }),
      });
      setSuccess(status >= 200 && status < 300);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      setError(error);
    }
  };
  return (
    <>
      <Notification
        onClose={handleClose}
        locale={locale}
        error={error}
        loading={loading}
        success={success}
      />
      <div
        className="container min-h-screen md:flex md:justify-between md:align-center md:pt-24 bg-gradient-to-b from-blue-dark to-black pb-10"
        id="hero"
      >
        <div className="pt-32 mx-auto md:flex-1 md:pt-16 text-center">
          <h1 className="text-5xl text-grey font-display text-center md:text-8xl">
            The decentralized <span className="text-white">knowledge</span> network{' '}
            <span className="text-white">fighting misinformation.</span>
          </h1>
          <p className="text-grey">{intro}</p>
          <div className="pt-10 md:flex md:max-w-md mx-auto border-blue md:border-b-2 text-white">
            <form id="newsletter_form" method="POST" onSubmit={handleSubmit} className="flex-1">
              <input
                type="email"
                name="email"
                id="email"
                placeholder="Enter email to subscribe newsletter"
                required
                className="w-full p-2 mr-1 bg-transparent border-blue border-b-2 text-white md:flex-1 md:border-none "
                onChange={event => setEmail(event.target.value)}
              />
              <input
                className="hidden"
                autoComplete="off"
                type="email2"
                id="email2"
                name="email2"
                placeholder="Your e-mail here"
                onChange={event => setHoneypotEmail(event.target.value)}
              />
            </form>
            <button
              disabled={!email || honeypotEmail || success}
              type="submit"
              form="newsletter_form"
              value="Submit"
              className="p-2 mt-2 rounded max-w-max flex items-center mx-auto transition duration-500 ease-in-out transform hover:-translate-y-1 hover:scale-110 disabled:cursor-not-allowed disabled:transform-none md:flex-1 md:mt-0 md:ml-2"
            >
              <img src={suscribeIcon} className="disabled:icon-blue icon-white" />
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default Hero;
