import React from 'react';

import checkIcon from '../images/icons/check.svg';
import crossIcon from '../images/icons/cross.svg';
import loadingIcon from '../images/icons/loading.svg';
import closeIcon from '../images/icons/close.svg';

interface Props {
  onClose: () => void
  locale: string;
  error: {
    message: string;
  };
  loading: boolean;
  success: boolean;
}

const types = {
  error: {
    color: 'white',
    backgroundColor: 'red',
    icon: crossIcon,
  },
  loading: {
    color: 'white',
    backgroundColor: 'blue',
    icon: loadingIcon,
  },
  success: {
    color: 'white',
    backgroundColor: 'green',
    message: {
      en: 'Thanks for your subscription ! You will receive an email from us to confirm your address, please check your spam if it does not appear in your inbox.',
      fr: "Merci pour votre souscription ! Vous allez recevoir un email de notre part pour confirmer votre adresse, veuillez vérifier vos spams si celui-ci n'apparaît pas dans votre boite de réception.",
    },
    icon: checkIcon,
  },
};

const Notification = ({ onClose, locale, error, loading, success }: Props) => {
  let content = undefined;
  if (error && error.message)
    content = {
      type: 'error',
      message: error.message,
      icon: types.error.icon,
      color: types.error.color,
      backgroundColor: types.error.backgroundColor,
    };
  if (loading)
    content = {
      type: 'loading',
      icon: types.loading.icon,
      color: types.loading.color,
      backgroundColor: types.loading.backgroundColor,
    };
  if (success)
    content = {
      type: 'success',
      icon: types.success.icon,
      color: types.success.color,
      backgroundColor: types.success.backgroundColor,
      message: types.success.message[locale],
    };

  if (content) {
    return (
      <div
        className={`flex flex-col items-center text-center bg-${content.backgroundColor} py-6 px-3 shadow-md mb-2 mx-auto z-50 w-full md:left-1/3 fixed md:w-2/6`}
      >
        <button onClick={onClose} className="absolute right-4 top-4">
          <img src={closeIcon} alt="close icon" className="h-4 icon-white" />
        </button>
        <div className="rounded-full mr-3">
          {content.icon && (
            <img
              src={content.icon}
              className={`mx-auto p-4 icon-${content.color} ${loading && 'animate-spin'}`}
            />
          )}
        </div>
        {content.message && (
          <div className={`text-${content.color} max-w-xs`}>{content.message}</div>
        )}
      </div>
    );
  }
  return null;
};

export default Notification;
